class FileStorageService {
  constructor() {
    this.fileName = 'deepwork-data.json';
    this.data = this.getInitialData();
    // CHANGED: h tr c open/save picker
    this.isFileAPISupported = ('showOpenFilePicker' in window) || ('showSaveFilePicker' in window);

    // NEW: gi handle file khi user chn  ng b th cng
    this.fileHandle = null;

    // Bind the save method to `this` to make it removable
    this.handleBeforeUnload = this.saveData.bind(this);
    
    // Load data from file or localStorage on init
    this.loadInitialData();
    
    // Auto-save every 30 seconds (mc nh: ch localStorage)
    this.autoSaveInterval = setInterval(() => {
      this.saveData();
    }, 30000);
    
    // Save before page unload (mc nh: ch localStorage)
    window.addEventListener('beforeunload', this.handleBeforeUnload);
  }

  // Get initial data structure
  getInitialData() {
    return {
      tasks: [],
      sessions: [],
      dailyTargets: {},
      settings: {
        defaultWorkDuration: 25,
        defaultBreakDuration: 5,
        defaultLongBreakDuration: 15,
        soundEnabled: true,
        notificationEnabled: true,
        autoStartBreaks: false,
        darkMode: false
      },
      metadata: {
        created: new Date().toISOString(),
        version: '1.0.0',
        lastSaved: new Date().toISOString()
      }
    };
  }

  // THAY I CHNH: Ti d liu khi khi ng
  async loadInitialData() {
    try {
      // 1. u tin ti t localStorage trc
      const localData = this.loadFromLocalStorage();
      if (localData && localData.tasks && localData.tasks.length > 0) {
        console.log(' Ti d liu t Local Storage.');
        this.data = localData;
        return;
      }

      // 2. Nu localStorage trng, ti t file deepwork-data.json mc nh
      console.log(' Local Storage trng, ti d liu t file mc nh trong /public.');
      const response = await fetch('/deepwork-data.json');
      if (!response.ok) {
        throw new Error(`Khng th ti file mc nh: ${response.statusText}`);
      }
      const fileData = await response.json();
      this.data = { ...this.getInitialData(), ...fileData };
      
      // Lu d liu va ti vo localStorage  dng cho cc ln sau
      this.saveToLocalStorage();
      console.log('  ti v ng b d liu t file vo localStorage.');

    } catch (error) {
      console.error(' Li khi ti d liu ban u:', error);
      // Nu c li, s dng d liu trng
      this.data = this.getInitialData();
    }
  }

  // Load from localStorage as fallback
  loadFromLocalStorage() {
    try {
      const tasks = JSON.parse(localStorage.getItem('deepwork_tasks_v3') || '[]');
      const sessions = JSON.parse(localStorage.getItem('deepwork_sessions_v3') || '[]');
      const dailyTargets = JSON.parse(localStorage.getItem('deepwork_daily_targets') || '{}');
      const settings = JSON.parse(localStorage.getItem('deepwork_settings') || '{}');
      const metadata = JSON.parse(localStorage.getItem('deepwork_metadata') || '{}');
      
      if (tasks.length > 0 || sessions.length > 0) {
        return {
          tasks,
          sessions,
          dailyTargets,
          settings: { ...this.getInitialData().settings, ...settings },
          metadata: { ...this.getInitialData().metadata, ...metadata }
        };
      }
    } catch (error) {
      console.error(' Li khi c t Local Storage:', error);
    }
    return null;
  }

  // Save to localStorage
  saveToLocalStorage() {
    try {
      const dataToSave = {
        ...this.data,
        metadata: {
          ...this.data.metadata,
          lastSaved: new Date().toISOString()
        }
      };

      localStorage.setItem('deepwork_tasks_v3', JSON.stringify(dataToSave.tasks));
      localStorage.setItem('deepwork_sessions_v3', JSON.stringify(dataToSave.sessions));
      localStorage.setItem('deepwork_daily_targets', JSON.stringify(dataToSave.dailyTargets));
      localStorage.setItem('deepwork_settings', JSON.stringify(dataToSave.settings));
      localStorage.setItem('deepwork_metadata', JSON.stringify(dataToSave.metadata));
      
      console.log('  lu vo localStorage');
    } catch (error) {
      console.error(' Li khi lu vo localStorage:', error);
      throw error;
    }
  }

  // THAY I CHNH: Lu (mc nh) CH vo localStorage
  async saveData() {
    try {
      // Update metadata
      this.data.metadata.lastSaved = new Date().toISOString();
      
      // 1) Lun lu localStorage
      this.saveToLocalStorage();

      // KHNG t ghi file  y na (ng b file s gi forceSave/saveToFile)
      console.log('  lu vo localStorage (mc nh).');
    } catch (error) {
      console.error(' Li khi lu d liu:', error);
    }
  }

  // NEW: Save to public/deepwork-data.json using a backend endpoint
  async saveToPublicFile() {
    try {
      // Trong mi trng development vi React, bn cn to mt endpoint API
      //  ghi file vo th mc public. y l mt v d vi fetch:
      
      const dataToSave = {
        ...this.data,
        metadata: {
          ...this.data.metadata,
          lastSaved: new Date().toISOString()
        }
      };

      // Gi endpoint backend  lu file
      const response = await fetch('/api/save-data', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(dataToSave)
      });

      if (!response.ok) {
        throw new Error(`Li khi lu file: ${response.statusText}`);
      }

      console.log('  lu vo file deepwork-data.json');
    } catch (error) {
      console.error(' Khng th lu vo file (c th cha c backend API):', error);
      // Khng throw error  khng gin on vic lu vo localStorage
    }
  }

  // Tasks operations
  async getTasks(includeArchived = false) {
    if (includeArchived) {
      return this.data.tasks;
    }
    return this.data.tasks.filter(task => !task.isArchived);
  }

  async addTask(taskName, description = null, defaultDuration = 25, color = '#3B82F6') {
    const newTask = {
      id: Date.now(),
      name: taskName,
      description,
      defaultDuration,
      color,
      isArchived: false,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };

    this.data.tasks.push(newTask);
    await this.saveData(); // T ng lu sau khi thm task
    
    return newTask;
  }

  async deleteTask(taskId) {
    // Remove task and all related sessions
    this.data.tasks = this.data.tasks.filter(task => task.id !== taskId);
    this.data.sessions = this.data.sessions.filter(session => session.taskId !== taskId);
    
    await this.saveData(); // T ng lu sau khi xa task
    return true;
  }

  // NEW: Update task name
  async updateTask(taskId, updates) {
    const taskIndex = this.data.tasks.findIndex(t => t.id === taskId);
    if (taskIndex === -1) {
      throw new Error('Task not found');
    }

    this.data.tasks[taskIndex] = {
      ...this.data.tasks[taskIndex],
      ...updates,
      updatedAt: new Date().toISOString()
    };

    await this.saveData();
    console.log(' Task updated:', this.data.tasks[taskIndex]);
    return this.data.tasks[taskIndex];
  }

  // NEW: Toggle task archive status
  async toggleTaskArchive(taskId) {
    const taskIndex = this.data.tasks.findIndex(t => t.id === taskId);
    if (taskIndex === -1) {
      throw new Error('Task not found');
    }

    this.data.tasks[taskIndex].isArchived = !this.data.tasks[taskIndex].isArchived;
    this.data.tasks[taskIndex].updatedAt = new Date().toISOString();

    await this.saveData();
    console.log(' Task archive toggled:', this.data.tasks[taskIndex]);
    return this.data.tasks[taskIndex];
  }

  // NEW: Get archived tasks
  async getArchivedTasks() {
    return this.data.tasks.filter(task => task.isArchived);
  }

  // Sessions operations
  async getSessions() {
    return this.data.sessions;
  }

  async addSession(taskId, duration, plannedDuration = null) {
    const newSession = {
      id: Date.now(),
      taskId: taskId,
      duration: duration,
      plannedDuration: plannedDuration,
      sessionType: 'work',
      completedAt: new Date().toISOString(),
      createdAt: new Date().toISOString()
    };

    this.data.sessions.push(newSession);
    await this.saveData(); // QUAN TRNG: T ng lu sau khi kt thc session
    
    console.log('  lu session mi:', newSession);
    return newSession;
  }

  // Daily targets operations
  async getDailyTarget(date = null) {
    const targetDate = date || new Date().toISOString().split('T')[0];
    const target = this.data.dailyTargets[targetDate];
    
    return {
      targetMinutes: target?.targetMinutes || 0,
      targetDate: targetDate
    };
  }

  async setDailyTarget(targetMinutes, date = null) {
    const targetDate = date || new Date().toISOString().split('T')[0];
    
    this.data.dailyTargets[targetDate] = {
      targetMinutes: targetMinutes,
      targetDate,
      createdAt: new Date().toISOString(),
      updatedAt: new Date().toISOString()
    };

    await this.saveData(); // T ng lu sau khi t mc tiu
    return this.data.dailyTargets[targetDate];
  }

  // NEW: Chn file ch  ng b (File System Access API)
  async selectFile() {
    if (!this.isFileAPISupported) {
      throw new Error('Trnh duyt khng h tr File System Access API');
    }
    const handle = await window.showSaveFilePicker({
      suggestedName: this.fileName,
      types: [
        {
          description: 'JSON File',
          accept: { 'application/json': ['.json'] }
        }
      ]
    });
    this.fileHandle = handle;
    this.fileName = handle.name || this.fileName;

    // Ghi ngay snapshot hin ti (ty chn)
    await this.saveToFile();

    return true;
  }

  // NEW: Ghi ra file  chn
  async saveToFile() {
    if (!this.fileHandle) {
      throw new Error('Cha chn file  ng b. Hy bm "Chn File".');
    }
    const dataToSave = {
      ...this.data,
      metadata: {
        ...this.data.metadata,
        lastSaved: new Date().toISOString()
      }
    };
    const writable = await this.fileHandle.createWritable();
    await writable.write(JSON.stringify(dataToSave, null, 2));
    await writable.close();
    console.log('  ng b d liu ra file:', this.fileName);
  }

  // NEW: c d liu t file  chn v np vo app (ng thi ghi vo localStorage)
  async loadFromFile() {
    // CHANGED: nu cha c fileHandle th m hp thoi chn file  c
    if (!this.fileHandle) {
      if ('showOpenFilePicker' in window) {
        const [handle] = await window.showOpenFilePicker({
          multiple: false,
          excludeAcceptAllOption: true,
          types: [
            {
              description: 'JSON File',
              accept: { 'application/json': ['.json'] }
            }
          ]
        });
        this.fileHandle = handle;
        this.fileName = handle.name || this.fileName;
      } else {
        throw new Error('Cha chn file  ti d liu.');
      }
    }

    const file = await this.fileHandle.getFile();
    const text = await file.text();
    const parsed = JSON.parse(text);

    // Merge an ton vi cu trc mc nh
    this.data = { ...this.getInitialData(), ...parsed };
    this.saveToLocalStorage();
    console.log('  np d liu t file v ng b vo localStorage.');
    return true;
  }

  // NEW: Fallback upload t <input type="file">
  async uploadFromInput(file) {
    const text = await file.text();
    const parsed = JSON.parse(text);
    this.data = { ...this.getInitialData(), ...parsed };
    this.saveToLocalStorage();
    console.log('  np d liu t file upload v ng b vo localStorage.');
    return true;
  }

  // Force save (manual sync) - GI s ng b ra file khi user bm nt
  async forceSave() {
    try {
      // Cp nht localStorage trc
      await this.saveData();
      // Sau  ghi ra file nu  chn file
      await this.saveToFile();
      // small delay to ensure FS completion
      await new Promise(r => setTimeout(r, 50));
      return true;
    } catch (e) {
      console.warn(' forceSave failed:', e);
      return false;
    }
  }

  // Download data as JSON file (fallback for backup)
  downloadAsFile() {
    try {
      const dataToSave = {
        ...this.data,
        metadata: {
          ...this.data.metadata,
          exportedAt: new Date().toISOString()
        }
      };

      const dataStr = JSON.stringify(dataToSave, null, 2);
      const dataBlob = new Blob([dataStr], { type: 'application/json' });
      const url = URL.createObjectURL(dataBlob);
      
      const link = document.createElement('a');
      link.href = url;
      link.download = `deepwork-backup-${new Date().toISOString().split('T')[0]}.json`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      
      URL.revokeObjectURL(url);
      console.log(' File backup  c ti xung');
    } catch (error) {
      console.error(' Li khi ti xung file:', error);
      throw error;
    }
  }

  // Check if File API is supported
  isFileAPIAvailable() {
    return this.isFileAPISupported;
  }

  // Get current file status
  getFileStatus() {
    return {
      isFileAPISupported: this.isFileAPISupported,
      hasFileHandle: !!this.fileHandle,
      fileName: this.fileHandle?.name || this.fileName,
      lastSaved: this.data.metadata?.lastSaved
    };
  }

  // Cleanup
  cleanup() {
    console.log(' Dn dp cc ti nguyn ca FileStorageService.');
    if (this.autoSaveInterval) {
      clearInterval(this.autoSaveInterval);
    }
    window.removeEventListener('beforeunload', this.handleBeforeUnload);
  }
}

export default new FileStorageService();