// src/utils/formatters.js

// Hm nh dng thi gian t giy sang gi, pht, giy
export const formatTime = (seconds) => {
  if (isNaN(seconds) || seconds < 0) return '0 giy';
  if (seconds < 60) return `${seconds} giy`;
  
  const h = Math.floor(seconds / 3600);
  const m = Math.floor((seconds % 3600) / 60);
  
  let parts = [];
  if (h > 0) parts.push(`${h} gi`);
  if (m > 0) parts.push(`${m} pht`);
  
  return parts.join(' ');
};

// Hm ly vn bn tng ng vi b lc
export const getFilterText = (filter) => {
  const filterMap = {
    day: 'hm nay',
    week: 'tun ny',
    month: 'thng ny',
  };
  return filterMap[filter] || '';
};